@import 'components/variables';

.section-perfil {
  padding-top: 50px;
  background-image: url(../images/team/bg-team-mobile.png);
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  @media (min-width: 992px) {
    background: #efeff7;
    background-image: url(../images/team/bg-team.png);
    background-position: center top;
    background-repeat: no-repeat;
    background-size: 100% auto;
    padding-top: 150px;
    padding-bottom: 3rem;
  }
  .content-container {
    background-color: transparent;
    padding: 0;
    @media (max-width: 991.98px) {
      max-width: 100%;
    }
    @media (min-width: 576px) {
      border-radius: 25px;
    }
    @media (min-width: 992px) {
      background: $white;
    }
  }
  .container1 {
    background: transparent;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    @media (min-width: 992px) {
      background: $white;
    }
    @media (max-width: 991.98px) {
      p {
        color: $white;
      }
    }
  }
  .container2 {
    background: $white;
    @media (max-width: 991.98px) {
      max-width: 100%;
      margin-top: 90px;
    }
    @media (min-width: 992px) {
      border-bottom-left-radius: 25px;
      border-bottom-right-radius: 25px;
      padding-bottom: 1.5rem;
    }
    .col-izq {
      display: none;
      border-right: 1px solid $gray-light;
      @media (min-width: 992px) {
        display: flex;
        justify-content: center;
        align-items: flex-start;
      }
      ul {
        padding-left: 0;
        list-style: none;
        li {
          &:before {
            background: $red;
          }
          &.active {
            &:before {
              left: 0;
              right: 0;
            }
          }
          .items {
            font-weight: bold;
            cursor: pointer;
            color: $black;
            text-decoration: none;
            transition: all 0.3s;
            &:hover {
              // color: $red;
            }
            p {
              margin-bottom: 0;
              padding: .5rem;
            }
            &.close-sesion {
              color: $red;
            }
          }
        }
      }
    }
    .col-der {
      order: 1;
      @media (min-width: 992px) {
        order: 2;
      }
      .content-fotoPerfil {
        @media (max-width: 991.98px) {
          height: 90px;
        }
        .fotoPerfil {
          position: relative;
          background-color: $white;
          border: 3px solid $red;
          width: 180px;
          height: 180px;
          margin: 0 auto;
          border-radius: 50%;
          cursor: pointer;
          @media (max-width: 991.98px) {
            transform: translateY(-90px);
          }
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
          }
          .cambiar-foto {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.4) 50%);
            color: $white;
            display: none;
            align-items: flex-end;
            justify-content: center;
            div {
              text-align: center;
              padding-bottom: 0.5rem;
            }
          }
          &:hover {
            .cambiar-foto {
              display: flex;
            }
          }
        }
      }
      .form-items {
        border-bottom: 1px solid #ced4da;
        .col-form-label {
          font-weight: bold;
        }
        .form-control {
          border: none;
          color: $text-gray;
          &:focus {
            background-color: #fff;
            border-color: $red;
            outline: 0;
            box-shadow: none;
          }
          &:disabled {
            background-color: #fff;
          }
        }
      }
      .form-textarea {
        .form-control {
          background-color: $gray-light;
          &:focus {
            color: #212529;
            background-color: #fff;
            border-color: $red;
            outline: 0;
            box-shadow: none;
          }
        }
      }
    }
  }
}

#cambiarFoto {
  .modal-dialog {
    .modal-content {
      .modal-header {
        .btn-close {
          &:focus {
            color: #212529;
            background-color: #fff;
            border-color: $red;
            outline: 0;
            box-shadow: none;
          }
        }
      }
      .modal-body {
        .form-control {
          &:focus {
            color: #212529;
            background-color: #fff;
            border-color: $red;
            outline: 0;
            box-shadow: none;
          }
        }
      }
      .modal-footer {
        .btn {
          &:focus {
            outline: 0;
            box-shadow: 0 0 0 0.25rem rgba(253, 69, 13, 0.25);
          }
        }
      }
    }
  }
}

#editarPerfil {
  .modal-dialog {
    .modal-content {
      .modal-header {
        .btn-close {
          &:focus {
            color: #212529;
            background-color: #fff;
            border-color: $red;
            outline: 0;
            box-shadow: none;
          }
        }
      }
      .modal-body {
        .form-control {
          &:focus {
            color: #212529;
            background-color: #fff;
            border-color: $red;
            outline: 0;
            box-shadow: none;
          }
        }
      }
      .modal-footer {
        .btn {
          &:focus {
            outline: 0;
            box-shadow: 0 0 0 0.25rem rgba(253, 69, 13, 0.25);
          }
        }
      }
    }
  }
}
